@charset "utf-8";
*{
    box-sizing: border-box !important;
}

#todo{
    position: absolute;
    /*left top으로 해야지 border 생겼을 때 변화x*/
    top: 20%;
    left: 20%;
    
    transform: translate(-50%,-50%); /*이게 들어가야 클릭이 쉬움*/

    z-index: 111;
    padding: 20px;
    border: 1px solid transparent;
}


#todoInput{
    appearance: none;

    border: 1px solid rgb(118, 118, 118);
    width: 300px;
    height: 20px;

    padding: 3px;
   
}
/* #todoInput:focus{
    box-shadow: 0 0 0 3px rgb(118, 118, 118);
    
} */

#todo ul, #todo li{
    list-style: none;
}

.todoList li{
    width: 300px;
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
}
.todoList li span{
  display: inline-block;
  cursor: pointer;
}

.todoList input:checked+div{
    text-decoration:line-through
}