@charset "utf-8";

#changeIcon{
    position: absolute;
    top: 32px;
    right: 32px;

    cursor: pointer;

}
#changeIcon img{
    opacity: 0.5;
    transition: 1.5s;

}

#changeIcon img[class='active']{
    opacity: 1;
}

#changeIcon img:hover{
    animation: rotateSetting 5s linear infinite;
}


.borderActive{
    border-color: black !important;
   
}

.active{
    animation: rotateSetting 5s linear infinite;
}

@keyframes rotateSetting {

    100%{transform: rotate(360deg);}
    
}