@charset "utf-8";

#clock{
    /* width: 340px; */
    position: absolute;
    left: 50%;
    top: 50%;

    transform: translate(-50%,-50%);

    font-size: 80px;
    font-weight: 700;


   padding: 20px;

   border: 1px solid transparent;
    
}

#clock span{
    font-size: 24px;
    font-weight: 400;
    display: inline-block;
    margin-left: 20px;
}